
.page-main__header--form {
  padding-top: 27px;
  padding-bottom: 39px;
  @media (min-width: $tablet-width) {
    padding: 0 40px;
  }
  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.page-main__heading {
  margin: 0; //        P   
  margin-bottom: 40px; //     .page-main__heading       40px
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: $font-heading;

  @media (min-width: $tablet-width) {
    margin-bottom: 0; //  margin-bottom: 32px;   ,      .      32       32                     . .
    font-size: 60px;
    line-height: 60px;
  }
}

.page-main__heading--catalog {
  @media (max-width: $mobile-width-only) {
    padding: 0 20px;
  }
  @media (min-width: $tablet-width) {
    grid-column: 1 / -1; //       
    margin-bottom: 22px; //   82      gap 60
  }
}


.page-main__heading--form {
  margin-bottom: 37px;
  @media (min-width: $tablet-width) {
    margin: 0 0 54px;
  }
}

.page-main__subheading {
  margin: 0;

  font-family: $font-heading;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  @media (min-width: $tablet-width) {
    margin: 0 0 66px;
    font-size: 20px;
    line-height: 30px; /* 150% */
  }
}