.page-footer {
  padding: 39px 0 22px;
  background-color: $basic_grey;
  @media (min-width: $tablet-width) {
    padding: 56px 0;
  }
}

.page-footer__container {
  //            ,       
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 164px 1fr 164px; //          163    (164px).        164            ,        164
    gap: 16px; //     ,           
    align-items: center;
  }
}

.page-footer__logo {
  @media (max-width: $mobile-width-only) { //           ,        
    margin-bottom: 32px;
  }
}

.page-footer__social {
  margin: 0;
  @media (max-width: $mobile-width-only) { //           ,        
    margin-bottom: 22px;
    border-top: 1px solid $special_grey-4;
    border-bottom: 1px solid $special_grey-4; //           social.scss
  }
}
