.contacts {
  background-color: $basic_grey;
  @media (min-width: $desktop-width) {
    position: relative;
    display: grid;
    z-index: 0; //         z-index
    min-height: 400px;
    padding: 40px 0 52px; // .      .

    grid-template-columns: 1220px; //    1440-110*2          110
    align-items: center; //        
    justify-content: center; //    1440 ( )     1220   justify-content: center;    

  }
}

.contacts__container { //     contacts__map
  padding: 26px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;

  @media (min-width: $tablet-width) {
    padding: 56px 40px; //        
    grid-template-columns: 172px 172px;
    justify-content: space-between; //               
  }

  @media (min-width: $desktop-width) {
    width: 565px;
    position: relative; //  position: static   z-.(   styles     
    margin: 0; //        left  right: auto(   )     ,  
    z-index: 1; //           z-index     html    
    padding: 56px 80px; //          40  80
    background-color: $white; //           .contact,    .contact__container
  }
}

.contacts__heading {
  margin: 0;
  padding-right: 10px;
  font-family: $font-heading;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  @media (min-width: $tablet-width) {
    padding-right: 0; //     space-between        
    font-size: 20px;
    line-height: 26px;
  }
}

.contacts__address {
  margin: 0;
  color: $special-dark-grey;
  font-family: $font-body;
  font-weight: 400;
  line-height: 20px; //  font-size   .page 14px
  @media (min-width: $tablet-width) {
    display: flex; //      span             
    flex-direction: column; //               
    justify-content: space-between; //   
    text-align: right; //     
  }
}

.contacts__map { //     .contacts         
  display: block; //   inline-block       - -
  width: 100%; //   
  height: 362px; //     

  border: none; //   ,     ,       .
  @media (min-width: $tablet-width) {
    height: 400px; //     
  }

  @media (min-width: $desktop-width) {
    position: absolute; //  .contacts   position: relative;      
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
