.promo {
  padding-top: 8px; //          
  text-align: center;
  @media (max-width: $mobile-width-only) {
    color: $white; //       
    background-color: $color-primary; //         
    background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 0px, rgba(104, 183, 56, 0.85) 302px, #ffffff 302px), //      (to bottom -  )    0px    302px (    )        302px
                      url("../../img/background/index-background-@x1.jpg"); //    
    background-position: 50% 0; //     () =50% ..   ,   () =0       
    background-repeat: no-repeat;
  }

  @media (min-width: $tablet-width) {
    color: $black; //  :   ,
    background-image: none; //  ,
    background-color: transparent; //  
    text-align: left; //     
  }

  @media (min-width: $desktop-width) { //       -        
    position: relative; //        
    &::after {
      content: "";
      position: absolute; //     (.promo)
      top: -200px; //       200px
      right: 0; //    
      bottom: 0; //    
      left: 50%; //      
      z-index: -2; //      ,    -1,         .promo__image   z-index        (promo::after)

      background-repeat: no-repeat;
      background-position: 0 100%; //                
      background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85) 0px, rgba(104, 183, 56, 0.85) 100%),
      url("../../img/background/index-background-720x694.jpg");
    }
  }
}

.promo__container { // e    c container,                
  @media (min-width: $tablet-width) {
    position: relative; //   -
    padding: 0 90px 526px; // 90px        , 526px   ( )    
  }
  @media (min-width: $desktop-width) {
    min-height: 582px; //    
    padding: 20px 610px 95px 80px; // 80px
  }
}

.promo__heading {  // :    ....
  margin: 0;
  margin-bottom: 25px;
  font-size: 36px;
  line-height: 1;
  font-family: $font-heading;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    margin-bottom: 41px;
    font-size: 60px;
  }

}

.promo__text {
  margin-top: 0;
  margin-bottom: 29px;

  font-family: $font-heading;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    margin-bottom: 49px;
  }
}

.promo__image {
  width: 280px;
  @media (min-width: $tablet-width) {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 709px;
    height: 609px;
  }
  @media (min-width: $desktop-width) {
    top: 0;
    left: calc(50% - 162px); //           162px;
    width: 552px;
    height: 499px;
  }
}

.promo__button {
  width: 100%;
}
