.logo { //     c @import "blocks/logo.scss";  style.scss
  display: grid; //   ,    .
  grid-template-columns: 34px 1fr; //           .page-header__bar,     logo.
  align-items: center; //           .page-header__logo
  gap: 16px; //              .page-header__bar

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: $tablet-width) {
    display: block;
  }
}


.logo__image-large {
  display: none;
  @media (min-width: $tablet-width) { //   -    
    display: block;
  }

  @media (min-width: $desktop-width) {
    transform: scale(1.161); //   174   202   202/174   //       (    https://stackoverflow.com/questions/24463096/change-svg-viewbox-size-with-css )
  }
}

.logo__sign {
  justify-self: center; //       
}

.page-footer__logo {
  grid-template-columns: auto;
  margin-left: auto;
  margin-right: auto;
  width: 128px;
  height: 24px;
}
