.example {
  padding: 25px 0 40px;
  background-color: $special_grey-6;

  @media (min-width: $tablet-width) {
    padding-top: 43px;
    padding-bottom: 56px;
    border-top: 1px solid $special_grey-5; //    process    example     
    background-image: linear-gradient(to top, $special_grey-6 462px, $white 462px); // - ,       462  
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 75px; //      43px
  }
}

.example__container { //        
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.example__content { //     
  margin-bottom: 40px;
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 366px 1fr;
    column-gap: 32px; //    
  }
  @media (min-width: $desktop-width ) {
    grid-template-columns: 366px 70px 1fr; // 366    dt  dl    gap 24px,  70 ?
    margin-bottom: 0; //          .example__content             40  
  }
}

.example__heading {
  @media (min-width: $tablet-width) {
    grid-column: 1 / -1; //    ,      less   -1,   scss     c grid
  }
}

.example__text {
  color: $special_dark-grey;
  margin: 0;
  margin-bottom: 20px;
  @media (min-width: $tablet-width) {
    grid-column: 1 / -1;
    margin-top: 63px; //       .example___text    page-main__heading,          
    margin-bottom: 54px;
  }
  @media (min-width: $desktop-width) {
    grid-column: 1 / 3; //     .example__content    ,       .
    margin-top: 146px;
    margin-bottom: 70px;

  }
}

.example__list {
  margin-bottom: 34px; //        example-definition  c           14         20px
  margin-top: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 32px;
  @media (min-width: $tablet-width) {
    margin-bottom: 0; //     ,    .example-content
  }
  @media (min-width: $desktop-width) {
    margin-bottom: 60px;
    grid-column: 1 / 2; // display grid     (example-content) ,      .  example-list     
    gap: 42px; //       
  }
}

.example__item {
  padding: 14px 8px 0;
  display: flex;
  flex-direction: column-reverse; //   
  align-items: center; //   
  border: 1px solid $special_grey;
  border-radius: 3px;

  @media (min-width: $tablet-width) {
    padding-top: 15px; //    1   
  }
}

 .example__title {
  width: 87px; //    77         10  87px
  margin-bottom: -10px; //         example__item   
  padding: 0 4px; //   
  text-align: center;
  background-color: $special_grey-6;
  color: $special-dark-grey;
  font-family: $font-body;
  font-size: 12px;
  font-weight: 400;
  line-height: 1;
  text-transform: lowercase;

  @media (min-width: $tablet-width) {
    width: auto; //               
    margin-bottom: -6px; //    example__item  5    6
    padding: 0 7px; //              example__item  
    background-color: $white; //        
    font-size: 14px; //   
  }

  @media (min-width: $desktop-width) {
    background-color: $basic_grey; //         
  }
}


.example__definition {
  margin-left: 0;  //   dd   ,  
  margin-bottom: 8px; //      exaple__title                 
  font-family: $font-heading;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  @media (min-width: $tablet-width) {
    font-size: 30px;
    line-height: 37px;
    margin-bottom: 11px; //     
  }
}

.example__spending {
  margin: 0;  //    <p>
  text-align: center;
  font-family: $font-heading;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    padding-left: 178px;
    text-align: right;
    margin-top: 11px;
  }
  @media (min-width: $desktop-width) {
    padding-left: 0;
    grid-column: 1 / 2;
    display: grid;
    grid-template-columns: 1fr 42px 1fr; //                html   span       grid
    text-align: left;

    span:first-child {
      grid-column: 1 / 2;
    }

    span:last-child {
      grid-column: 3 / 4;
      justify-self: center;
    }
  }
}

//  
.example__images {
  margin-bottom: 15px;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 50%;
    padding: 0 58px;
    overflow: hidden; //      
  }
  @media (min-width: $desktop-width) {
    padding: 0;
    width: 592px;
  }
}

.example__image {
  display: none;
  overflow: hidden;
  width: 280px;
  height: 232px;
  place-content: center;
  @media (min-width: $tablet-width) {
    width: 296px;
    height: 511px;
    display: grid;
    justify-content: start;

    img {
      margin-left: -29px;  //    (                         margin-left     
      max-width: none;
      height: 511px;
      width: auto;  //        511      (justify-content: start)      (justify-content: end)    
    }

    &:last-child { //   (img)  
      justify-content: end;
      img {
        margin-right: -2px; //   .         -        img   ,           
      }
    }
  }
}

.example__image--active {
  @media (max-width: $mobile-width-only) {
    display: flex;
  }
}
