.main-nav {
  position: relative;
  font-family: $font-heading;
  font-weight: 500;
  color: $black;

  @media (max-width: $mobile-width-only) { //     (     767px)
    border-bottom: 1px solid $special_grey-5; //       1px  .             
  }

  //     .page-header__nav     <header>,   <body> ,   <html> ,       ,     .       :
  @media (min-width: $tablet-width) {
    width: 708px; //   page-header__nav  <header>,   <body> ,   <html> ,        30px : (768px - 2*30px)
    margin: 0 auto; //       page-header__nav   <header>,   <body> ,   <html> ,        768px
    padding: 24px 0; //       24px            .
    display: grid; //    page-header__nav    768px
    grid-template-columns: 174px 1fr; // 174px           174px,           1fr        
    gap: 16px; //      ,   16px,    (,  ,  )     .
  }

  @media (min-width: $desktop-width) { //          page-header__nav   1440px,           110px*2 : 1440-110*2=1220 px
    width: 1220px;
  }
}

.main-nav__bar {
  @media (max-width: $mobile-width-only) { //     (     767px)
    width: 320px; //                    
    margin: 0 auto; //       (page-header__nav      <header>,   <body> ,   <html> ,   )
    padding: 12px 20px 13px;
    display: grid; //  page-header__bar     .page-header__logo(    )  .page-header__toggle
    grid-template-columns: 34px 1fr 34px; //     . 34px       ,    24px.     (           justify-self: center;      ,  ,     34px.                      34px  1fr .       .page-header__bar           .
    gap: 16px; //      -     .
    align-items: center; //      
  }
}

.main-nav__toggle {
  border: none; //        
  width: 40px; //  -       ,        ... 24px
  height: 40px;
  background-color: transparent;
  position: relative;
  display: block;

  @media (min-width: $tablet-width) { //     768 px   () (            768 px)        .
    display: none;
  }
}

.main-nav--nojs {
  .main-nav__toggle {
    display: none;
  }
}


.main-nav__wrapper {
  @media (max-width: $mobile-width-only) {
    position: absolute;
    top: 100%;
    width: 100%;
  }
}

.main-nav--opened .main-nav__toggle {
  &::before,
  &::after { //      (  )      
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0px;
    margin: auto;
    content: "";
    width: 24px;
    height: 1.5px;
    background-color: $black;
  }
  &::after {
    transform: rotate(45deg);
  }
  &::before {
    transform: rotate(-45deg);
  }
}

.main-nav--closed .main-nav__toggle {
  &::before { //      (  )      
    position: absolute;
    top: 12px;
    //   right: 0; left: 0; margin-right: 0; margin-left: 0;
    right: 0;
    left: 0;
    margin: 0 auto;
    content: "";
    width: 24px;
    height: 1.5px;
    background-color: $black;
    box-shadow: 0 8px 0 0 $black,
                0 16px 0 0 $black;
  }
}

.main-nav--closed .main-nav__wrapper {
  @media (max-width: $mobile-width-only) {
    display: none;
  }
}


.main-nav--nojs {
  .main-nav__toggle {
    display: none;
  }

  .main-nav__wrapper {
    position: static;
    display: block;
  }
}
