.page-header {
  @media (min-width: $desktop-width) {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      z-index: -2;
      top: 0;
      left: 50%;
      right: 0;
      bottom: 0;
      // background-color: rgba(104, 183, 56, 0.85);
    }
  }
}

.page-header__logo {
  grid-column-start: 1; //      ,  sass    grid-colomn: 1 / 3;      css     grid-column: 0.33333...      css  grid-column: 1/3; .
  grid-column-end: 3; //               page-header__nav;
  //          logo      (     logo.scss)

  .logo__image,
  .logo__sign {
    @media (min-width: $tablet-width) {
      display: none;
    }
  }

}

