
.catalog-sections {
  padding-top: 20px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
  }

  @media (min-width: $desktop-width) {
    padding-top: 82px;
  }
}

.catalog-section__container {
  @media (min-width: $desktop-width) { //               80px(   gap)
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.catalog-sections__item {
  position: relative; //      .        .
  padding: 24px 20px 18px; // 24px           , 20px    (  33px       ) 18px         

  background-color: $basic_grey;   //      catalog-sections__item
  @media (min-width: $tablet-width) {
    padding: 77px 316px 52px 53px;
  }
  @media (min-width: $desktop-width) {
    padding: 47px 55px 59px; //          (     
  }

  & + & { //  " "                
    margin-top: 20px; //       .catalog-sections__item

    @media (min-width: $tablet-width) {
      margin-top: 30px;
    }
    @media (min-width: $desktop-width) {
      margin-top: 0; //     ,         

    }
  }
}

.catalog-sections__heading {
  position: relative; //             position: relative     position: absolute; -     .catalog-sections__heading,         ,        .catalog-sections__item.          
  min-height: 50px; //    50px     
  margin: 0; //        h3 */
  margin-bottom: 22px; //            
  padding-left: 68px; //   50px + 19px              ( 69px   )
  display: flex; //      ( )         h3       
  align-items: center; //       . .  (        ,      .


  font-family: $font-heading;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    position: static; //        html.                     .catalog-sections__item       position: relative;
    padding-left: 0; //                 
    font-size: 36px;
  }

  @media (min-width: $desktop-width) {
    position: relative; //          
    padding-left: 162px; //   100px +    62px
    min-height: 100px; //               
    margin-bottom: 39px;
  }

  &::before,
  &::after {
    position: absolute;
    content: "";
  }

  &::before { //  ::before            ,       ::after
    top: 0;
    left: 0;
    background-color: $color-primary;
    border-radius: 50%;
    @include size(50px); //    mixins.scss.        width: 50px; height: 50px;

    @media (min-width: $tablet-width) {
      @include size(200px);  //  =  = 200px
      top: calc(50% - 100px); //  50%     .catalog-sections__item      .catalog-sections__heading  position: static;,       ,   .catalog-sections__item(  .catalog-sections__heading )   .  100px   top:      ,     .
      right: 62px; //          (  62 px);
      left: auto; //   left      left: 0;
    }

    @media (min-width: $desktop-width) { //     
      @include size (100px); //   mixin       . .  =  = 100px
      top: calc(50% - 50px);  // 50 %     (.catalog-sections__heading) 50px              .catalog-sections__heading
      right: auto; //             
      left: 0; //     
    }
  }

  &::after { //     
    background-size: contain; //    background-size: contain,    ,        .      background-size  background-repeat           
  }
}

.catalog-sections__heading--slim { //       
  &::after {
    @include size (36px, 50px); //   mixin
    top: -3px; //     3px  
    left: 7px; //     7px  
    background-image: url("../../img/icons/cat-slim.svg");

    @media (min-width: $tablet-width) { //  
      @include size (140px, 194px); //     
      top: calc(50% - 111px); //   .catalog-sections__item    11px   50%   100px,   111px
      left: auto; //   .
      right: 92px; //        
    }

    @media (min-width: $desktop-width) { //  
      @include size (70px, 97px); //     
      top: calc(50% - 56px); //   .catalog-sections__heading    6px   50%   50px,   56px
      left: 15px; //   .
      right: auto; //        
    }
  }
}

.catalog-sections__heading--pro { //   cat-pro   
  &::after { //        
    @include size (67px, 28px);
    top: 11px; //   .catalog-sections__heading      11px  
    left: -9px;
    background-image: url("../../img/icons/cat-pro.svg");

    @media (min-width: $tablet-width) { //  
      @include size (268px, 112px); //     
      top: calc(50% - 56px); //      .catalog-sections__item,               .catalog-sections__item,   50%-100px (100px   ),     50%     112/2=56              .catalog-sections__item(  .catalog-sections__heading     position: static      ,   .catalog-sections__item      position: relative)
      left: auto; //   .
      right: 28px; //        
    }

    @media (min-width: $desktop-width) { //  
      @include size (134px, 56px); //     
      top: calc(50% - 30px); //   .catalog-sections__heading   20px   50%   50px,   50-20=30px
      left: -19px; //   .
      right: auto; //        
    }
  }
}


.catalog-sections__text {
  margin: 0; //      p
  padding-bottom: 23px; //           -      .catalog-sections__text
  margin-bottom: 15px; //    15px       - 
  color: $special_dark-grey;
  border-bottom: 1px solid $special_grey-4;

  @media (min-width: $tablet-width) {
    color: $black; //        
    border-bottom: none; //       
    padding-bottom: 0; //        
    margin-bottom: 48px; //    -  
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 23px; //       
  }
}


.catalog-sections__link {
  font-family: $font-heading;
  color: $black;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;

  display: grid;
  grid-template-columns: max-content 32px; //         
  align-items: center; //     
  gap: 17px; //     

  &::after { //   
    content: "";
    width: 24px; //     ,       ,       (   -  )        32px              -.   :hover :active        32px        
    height: 10px; //     
    background-image: url("../../img/icons/icon_arrow.svg");
    background-position: 100% 50%; //  :   100%  -      100%      (   0(%)   ),      
    background-repeat: no-repeat; //                       
    //     -     svg
  }

  &:hover::after {
    width: 32px; //             
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) { //       20px;
    font-size: 20px;
    line-height: 20px;
  }
}
