.card-row {
  padding: 13px 0 19px;
  border-top: 1px solid $special_grey;
  &:last-of-type {
    border-bottom: 1px solid $special_grey; //     
  }

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr auto;
    justify-content: end;
    padding: 26px 38px 28px;
    border-left: 1px solid $special_grey;
    border-right: 1px solid $special_grey;
  }

  @media (min-width: $desktop-width) {
    padding: 16px 0;
    align-items: center;
    grid-template-columns: 305px 305px 245px; //          .card-row__list                245                 245         20    245+(80-20) =305           (245  80  245  80  245 80 245 )    305,   245   ""     
    gap: 20px;
    border-left: none;
    border-right: none;
  }
}

.card-row__heading {
  font-family: $font-heading;
  margin: 0;
  margin-bottom: 13px;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    margin-bottom: 25px;
    font-size: 20px;
  }
  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.card-row__list {
  margin: 0;
  margin-bottom: 15px;
  display: grid;
  grid-template-columns: 1fr auto; //            (     space-between)

  font-size: 14px;
  line-height: 1;

  @media (min-width: $tablet-width) {
    gap: 49px;
    justify-items: end;  //               justify-content,    justify-items      align-content b align-items
    align-items: end; //  card-row__list    22   card-row__heading   -  card-row__heading     25,        ,  -     
    grid-template-columns: 1fr minmax(44px,auto); //        250  50       
    margin-bottom: 22px;

    font-size: 16px;
    line-height: 125%;
  }
  @media (min-width: $desktop-width) {
    margin-bottom: 0;
    justify-items: start;//       
  }
}

.card-row__definition {
  margin-left: 0;
}

.card-row__button {
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  @media (min-width: $tablet-width) {
    grid-column: 1 / -1;
  }
  @media (min-width: $desktop-width) {
    grid-column: auto;
  }
}
