// site-menu   page-header__nav,             

.site-menu {
  margin: 0;
  padding: 0;
  list-style-type: none; //   site-menu   ,        .


  @media (max-width: $mobile-width-only) {
    font-size: 20px; //     20px : 500,
    font-weight: 500; //      500
    border-top: 1px solid $special_grey-5; //   1px     (      )      
    text-align: center; //         
    background-color: $white; //                  
  }

  @media (min-width: $tablet-width) {
    font-size: 18px; //         768px   18px
    line-height: 24px; /* 133.333% */
    display: flex; //            .    ,                  .
    flex-wrap: wrap;
    justify-content: flex-end; //        page-header__nav
  }

  @media (min-width: $desktop-width) {
    margin-left: auto;
    width: 520px;
  }
}

.site-menu__item {
  @media (max-width: $mobile-width-only) {
    border-bottom: 1px solid $special_grey-5; //          .     
  }
}

.site-menu__item--active { //  ()     
  @media (min-width: $tablet-width) {
    position: relative; //           

    &::after { //        site-menu
      position: absolute; //        relative    .site-menu__item--active
      bottom: 8px; //     .site-menu__item--active  8px
      left: 18px; //            site-menu__link
      right: 18px; //            site-menu__link
      content: ""; //    
      height: 2px; //  
      background-color: $color-primary; //  
    }
  }
}


.site-menu--index .site-menu__item--active{  //          - 
  @media (min-width: $desktop-width) { //           
    &::after {
      background-color: $white;
    }
  }
}

.site-menu__link {
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
  font-family: $font-heading; //         
  line-height: 1; //    1(    )

  display: block; //       inline       
  padding: 22px 20px 24px; //   
  color: $black; //    ,   -    ,
  text-decoration: none; //        

  &:not([href]) {
    cursor: default;
  }
  @media (max-width: $mobile-width-only) {

    &[href]:hover {
      background-color: $basic_grey;
    }

    &[href]:active {
      background-color: $special_grey-6;
    }
  }

  @media (min-width: $tablet-width) { //          
    padding: 17px; //        34px       17px     17px
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;

    &[href]:hover {
      color: fade-out($black, 0.4); // 1 - opacity 0.6 = 0.7
    }

    &[href]:active { //         
      color: fade-out($black, 0.7); // 1 - opacity 0.3 = 0.7
    }
  }


  @media (min-width: $desktop-width) {
    color: $special_black;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
  }
}

.site-menu--index .site-menu__link {
  @media (min-width: $desktop-width) {
    color: $white; //          

    &[href]:hover {
      color: fade-out($white, 0.4);
    }
    &[href]:active { //
      color: fade-out($white, 0.7);
    }
  }
}
